import sPopup from '@/components/s-popup/index';
export default{
	 components: {
	    sPopup
	  },
	data(){
		return {
			data:'',
			logined:false,
			  visible: false,
			  visible2:false,
			  advImg:'',
			  invite_code:'',
			  imgBaseUrl:'',
			  userInfo:{}
		}
	},
	onShow(){
		// this.imgBaseUrl=this.$imgBaseUrl
		var userInfo=uni.getStorageSync('userInfo')
		if(userInfo!='' & userInfo!=null & userInfo!=undefined){
			this.userInfo=userInfo
			this.getUserInfo(userInfo)
			this.logined=true
		}else{
			this.logined=false
		}
	},
	methods:{
		to(where){
			var ce=this.$operateInterceptor(this.logined)
			if(!ce){
				return ;
			}
			uni.navigateTo({
				url:`/pages/mine/${where}`
			})
		},
		to2(){
			var ce=this.$operateInterceptor(this.logined)
			if(!ce){
				return ;
			}
			uni.navigateTo({
				url:`/pages/tabBar2/tabMain`
			})
		},
		toMyOrder(active){
			var ce=this.$operateInterceptor(this.logined)
			if(!ce){
				return ;
			}
			uni.navigateTo({
				url:`/pages/mine/myOrder?active=${active}`
			})
		},
		toMyOrderDetail(){
			var ce=this.$operateInterceptor(this.logined)
			if(!ce){
				return ;
			}
			uni.navigateTo({
				url:`/pages/mine/myOrderDetail?active=5`
			})
		},
		tologin(){
			var ce=this.$operateInterceptor(this.logined)
			if(!ce){
				return ;
			}
			uni.navigateTo({
				url:`/pages/login/loginMobile`
			})
		},
		toRetail(){
			var ce=this.$operateInterceptor(this.logined)
			if(!ce){
				return ;
			}
			uni.navigateTo({
				url:`/pages/retail/retailCenter`
			})
		},
		toSetting() {
		    uni.navigateTo({
		    	url:`/pages/mine/settings`
		    })    
		},
		test(){
			uni.navigateTo({
				url:'../test'
			})    
		},
		getUserInfo(u){
			var that=this
			var url='&r=api.member.my&uid='+u.uid+'&token='+u.token
			  this.$apiPost(url).then((res) =>{
				that.data=res.data	
			  })
		},
		wxlogin(provider) {
		    uni.login({
		        provider: 'qq',



		        success: (res) => {
		            console.log('login success:', res);
		            // 更新保存在 store 中的登录状态
					uni.getUserInfo({
					      provider: 'qq',
					      success: function (infoRes) {
					        console.log('用户昵称为：' + infoRes.userInfo.nickName);
					      }
					    });
		        },
		        fail: (err) => {
		            console.log('login fail:', err);
		        }
		    });
		},
		showAdv(){
			var ce=this.$operateInterceptor(this.logined)
			if(!ce){
				return ;
			}
			var that=this
			var params={
				uid:this.userInfo.uid,
				token:this.userInfo.token,
			}
			var url='&r=api.common.share.createPoster'
			  this.$apiPost(url,params).then((res) =>{
				  that.advImg=res.data.img
				  that.visible=true
			  })
		},
		showInvite(locked){
			var ce=this.$operateInterceptor(this.logined)
			if(!ce){
				return ;
			}
			if(locked && locked==0){
				this.visible2=true
			}
		},
		saveImg(url){
			var that=this
			that.$loading()
			uni.downloadFile({
					url: url,
					success: (res) =>{
						if (res.statusCode === 200){
							uni.saveImageToPhotosAlbum({
								filePath: res.tempFilePath,
								success: function() {
									that.$msg('保存成功，请到相册中查看')
									that.visible=false
								},
								fail: function() {
									that.$msg('保存失败')
								},
								complete:() =>{
									uni.hideLoading()
								}
							});
						}
					}
				})
		},
		saveInvite(){
			var that=this
			var params={
				uid:this.userInfo.uid,
				token:this.userInfo.token,
				inviteCode:this.invite_code
			}
			var url='&r=api.member.my.invite'
			  this.$apiPost(url,params).then((res) =>{
				  that.$msg('绑定邀请人成功')
				  setTimeout(function(){
					  that.visible2=false
					  that.getUserInfo(that.userInfo)
				  },1000)
			  })
		}
	}
}
